.page-v2 .image-list {
  background-color: #dedede;
  display: flex;
  flex-flow: row wrap;
}
.page-v2 .image-list img {
  width: 100px;
  margin: 10px;
  padding: 4px;
  border: 1px solid red;
}
.page-v2 .bgc-image {
  display: flex;
  justify-content: space-between;
}
.page-v2 .bgc-image li {
  width: 100px;
  height: 100px;
}
.page-v2 .bgc-image .bgc-1 {
  width: 100px;
  height: 100px;
  background: url(@/assets/images/1.jpeg) center / contain no-repeat;
}
.page-v2 .bgc-image .bgc-2 {
  background: url(@/assets/images/6.gif) center / contain no-repeat;
}
.page-v2 .bgc-image .bgc-3 {
  background: url(@/assets/images/8.png) center / contain no-repeat;
}
.page-v2 .bgc-image .bgc-4 {
  background: url(@/assets/images/21.jpeg) center / contain no-repeat;
}
.page-v2 .font i {
  color: red;
  font-size: 20px;
  font-weight: 700;
  margin: 20px;
}
